import "./index.scss"

console.log("working")

wp.blocks.registerBlockType('mdplplugin/do-pobrania-link', {
    title: "Link do podstrony z materiałami do pobrania",
    icon: "editor-ul",
    category: "common",
    attributes: {
        text: { type: "string" },
        link: { type: "string" }
    },
    description: 'Dodaj na stronie "Do pobrania" link do osobnej podstrony z materiałami',
    example: {
        attributes: {
            text: "Tutaj wpisz tekst",
            link: "#"
        }
    },
    edit: EditComponent,
    save: (props) => {
        return null
    }
})

function EditComponent (props) {

    return (
        <div className="download-link-edit-block">
            <p className="download-link-headline">Generator linku do podstrony z materiałami do pobrania</p>
            <input type="text" name="mdplText" value={props.attributes.text} onChange={e => props.setAttributes({ text: e.target.value })} placeholder="Tutaj wpisz to, co ma być wyświetlane na stronie." />
            <input type="text" name="mdplLink" value={props.attributes.link} onChange={e => props.setAttributes({ link: e.target.value })} placeholder="Tutaj wklej link do odpowiedniego pliku." />
        </div>
    )
}